


export const serialize = (...args) => args.length ? JSON.stringify(...args) : ''

export const deserialize = string => JSON.parse(string || '{}')

export const load = key => JSON.parse(window.localStorage && localStorage.getItem(key) || '{}')

export const save = (key, obj) => window.localStorage && localStorage.setItem(key, serialize(obj))

export const purge = key => window.localStorage && localStorage.removeItem(key)
