import React, {useState, useEffect} from 'react';
import logo from './logo.svg';
import {load, save, purge, serialize} from './lib/io'
import './App.css';

function App() {

  const duration = 30 * 1000;
  const threshold = 10 * 1000;

  const EPOCH = -7200000
  const initialState = {
    startTime: EPOCH,
    endTime: EPOCH,
  }
  const [ellapsedTime, setEllapsedTime] = useState(0)
  const [state, setState] = useState(initialState)
  const [laps, setLaps] = useState([])

  useEffect(() => {
    const savedState = load('countdown-state')
    setState(savedState.state)
    setLaps(savedState.laps)
  }, [])

  useEffect(() => {
    const unloadListener = () => {
      save('countdown-state', {state, laps})
    }
    window.addEventListener('beforeunload', unloadListener)
    return () => {
      window.removeEventListener('beforeunload', unloadListener)
    }
  }, [])

  useEffect(() => {
    const intervalId = window.setInterval(() => {
      state.paused || setEllapsedTime(state.running ? Date.now() - state.startTime : state.endTime - state.startTime)
    }, 100)
    return () => {
      window.clearInterval(intervalId)
    }
  }, [state.running, state.startTime, state.endTime, state.paused])

  // todo - shold all of these commands be events? in order to write date based timer, we need to know when most of the things happen. at least start time and end time (and laps of course), but not necessarily pause time

  return (
    <div className="App">
      <header className="App-header">

        <button onClick={() => !state.running && setState({startTime: Date.now(), endTime: Date.now(), running: true})}>start</button>
        <button onClick={() => state.running && !state.paused && setState({...state, paused: true})}>pause</button>
        <button onClick={() => state.running && state.paused && setState({...state, paused: false})}>resume</button>
        <button onClick={() => state.running && setState({...state, endTime: Date.now(), running: false})}>stop</button>
        <button onClick={() => state.running && !state.paused && setLaps([...laps, Date.now()])}>lap</button>
        <button onClick={() => setState(initialState)}>reset</button>
      </header>
      <hr/>
      <main>
        <code>          {
                    // todo - use reduce to copare prev time and curr time
                    (laps || [])
                    .map(time => <p>{time - state.startTime}</p>)
                  }
</code>
        <pre>
          {ellapsedTime}
        </pre>
      </main>
      <hr/>
      <div>
        <pre>
          state: {serialize(state, null, 2)}
        </pre>
        <pre>
          laps: {serialize(laps, null, 2)}
        </pre>
      </div>
    </div>
  );
}

export default App;
